html {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden; /* Only on body, not html - to preserve sticky */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  min-height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* ========== Uniform Tile Sizing ========== */
.nx-tile {
  min-height: 140px;
  display: flex;
  flex-direction: column;
}

.nx-tile .card {
  height: 100%;
  display: flex;
  flex-direction: column;
  border-left: 6px solid transparent;
  border-top: none;
  border-right: 1px solid var(--border-color);
  border-bottom: 1px solid var(--border-color);
}

.nx-tile .card h2 {
  flex-shrink: 0;
}

.nx-tile .card > div:last-child {
  flex: 1;
  overflow-y: auto;
}

/* Unique border-left colors for each tile */
.nx-tile:nth-child(1) .card {
  border-left-color: #0ea5e9 !important; /* sky blue */
}

.nx-tile:nth-child(1) .card:before {
  background: rgba(14, 165, 233, 0.08);
}

.nx-tile:nth-child(2) .card {
  border-left-color: #8b5cf6 !important; /* violet */
}

.nx-tile:nth-child(2) .card:before {
  background: rgba(139, 92, 246, 0.08);
}

.nx-tile:nth-child(3) .card {
  border-left-color: #14b8a6 !important; /* teal */
}

.nx-tile:nth-child(3) .card:before {
  background: rgba(20, 184, 166, 0.08);
}

.nx-tile:nth-child(4) .card {
  border-left-color: #10b981 !important; /* emerald */
}

.nx-tile:nth-child(4) .card:before {
  background: rgba(16, 185, 129, 0.08);
}

.nx-tile:nth-child(5) .card {
  border-left-color: #f59e0b !important; /* amber */
}

.nx-tile:nth-child(5) .card:before {
  background: rgba(245, 158, 11, 0.08);
}

.nx-tile:nth-child(6) .card {
  border-left-color: #f43f5e !important; /* rose */
}

.nx-tile:nth-child(6) .card:before {
  background: rgba(244, 63, 94, 0.08);
}

.nx-tile:nth-child(7) .card {
  border-left-color: #6366f1 !important; /* indigo */
}

.nx-tile:nth-child(7) .card:before {
  background: rgba(99, 102, 241, 0.08);
}

.nx-tile:nth-child(8) .card {
  border-left-color: #ec4899 !important; /* pink */
}

.nx-tile:nth-child(8) .card:before {
  background: rgba(236, 72, 153, 0.08);
}

/* Light theme (default) */
body.light {
  --bg-color: #f5f5f5;
  --card-bg: white;
  --text-color: #333;
  --text-secondary: #666;
  --text-muted: #999;
  --border-color: #ddd;
  --shadow: rgba(0,0,0,0.1);
  --button-bg: #3b82f6;
  --button-hover: #2563eb;
  --header-bg: #f8f9fa;
  --success-color: #28a745;
  --error-color: #dc3545;
  --input-bg: white;
  --hover-bg: #f0f0f0;
  --accent-color: #00d4ff;
  --button-secondary-bg: #6c757d;
  --button-text: white;
}

/* Dark theme */
body.dark {
  --bg-color: #1a1a1a;
  --card-bg: #2d2d2d;
  --text-color: #e0e0e0;
  --text-secondary: #ccc;
  --text-muted: #888;
  --border-color: #444;
  --shadow: rgba(0,0,0,0.3);
  --button-bg: #4f46e5;
  --button-hover: #4338ca;
  --header-bg: #333;
  --success-color: #40c057;
  --error-color: #fa5252;
  --input-bg: #1e1e1e;
  --hover-bg: #3a3a3a;
  --accent-color: #00d4ff;
  --button-secondary-bg: #555;
  --button-text: white;
}

/* Dark mode input styling - easier on the eyes */
body.dark input[type="text"],
body.dark input[type="password"],
body.dark input[type="email"],
body.dark input[type="url"],
body.dark input[type="number"],
body.dark input[type="search"],
body.dark input[type="tel"],
body.dark input:not([type]),
body.dark select,
body.dark textarea {
  background-color: #2a2a2a !important;
  color: #e0e0e0 !important;
  border-color: #444 !important;
}

body.dark input[type="text"]::placeholder,
body.dark input[type="password"]::placeholder,
body.dark input[type="email"]::placeholder,
body.dark input[type="url"]::placeholder,
body.dark input[type="number"]::placeholder,
body.dark input[type="search"]::placeholder,
body.dark input[type="tel"]::placeholder,
body.dark input:not([type])::placeholder,
body.dark textarea::placeholder {
  color: #888 !important;
}

body.dark input[type="text"]:focus,
body.dark input[type="password"]:focus,
body.dark input[type="email"]:focus,
body.dark input[type="url"]:focus,
body.dark input[type="number"]:focus,
body.dark input[type="search"]:focus,
body.dark input[type="tel"]:focus,
body.dark input:not([type]):focus,
body.dark select:focus,
body.dark textarea:focus {
  background-color: #333 !important;
  border-color: #4f46e5 !important;
  box-shadow: 0 0 0 2px rgba(79, 70, 229, 0.3) !important;
}

body.dark select option {
  background-color: #2a2a2a;
  color: #e0e0e0;
}

.app-container {
  min-height: 100vh;
  background-color: var(--bg-color);
  color: var(--text-color);
  padding: 1rem;
  padding-top: 0; /* Remove top padding so sticky header works */
  transition: background-color 0.3s ease, color 0.3s ease;
  /* No overflow restrictions - required for sticky to work */
}

.dashboard {
  max-width: 1200px;
  margin: 0 auto;
}

/* ========== Sticky Sub-Navigation ========== */
.nx-subnav {
  position: -webkit-sticky !important;
  position: sticky !important;
  top: 62px !important;
  z-index: 800 !important;
  background-color: var(--bg-color);
  padding-top: 1rem;
  border-bottom: 2px solid var(--border-color);
  display: flex;
  gap: 0.25rem;
}

/* Ensure no ancestor breaks sticky behavior */
.dashboard {
  overflow: visible !important;
}

.header {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: var(--text-color);
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  margin-bottom: 1rem;
}

.card {
  background-color: var(--card-bg);
  color: var(--text-color);
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 2px 4px var(--shadow);
  border: 1px solid var(--border-color);
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}

.card h2 {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: var(--text-color);
}

.upload-section {
  background-color: var(--card-bg);
  color: var(--text-color);
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 2px 4px var(--shadow);
  border: 1px solid var(--border-color);
  margin-bottom: 1rem;
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}

.upload-section h2 {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  color: var(--text-color);
}

.button {
  background-color: var(--button-bg);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button:hover {
  background-color: var(--button-hover);
}

.preroll-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
}

.preroll-list {
  background-color: var(--card-bg);
  color: var(--text-color);
  border: 1px solid var(--border-color);
  border-radius: 0.25rem;
  padding: 0.75rem;
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}

.preroll-item {
  background-color: var(--card-bg);
  color: var(--text-color);
  border: 1px solid var(--border-color);
  padding: 0.5rem;
  border-radius: 0.25rem;
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
}

.preroll-item img {
  width: 100%;
  height: 5rem;
  object-fit: cover;
  border-radius: 0.25rem;
}


.tab-buttons {
  position: sticky;
  top: 0;
  z-index: 9999;
  display: flex;
  margin-bottom: 1rem;
  border-bottom: 1px solid var(--border-color);
  background-color: var(--header-bg);
  border-radius: 0.5rem 0.5rem 0 0;
  box-shadow: 0 2px 4px var(--shadow);
}

.tab-button {
  padding: 0.75rem 1.5rem;
  border: none;
  background: transparent;
  color: var(--text-color);
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: all 0.3s ease;
}

.tab-button.active {
  background-color: var(--button-bg);
  color: white;
  border-bottom-color: var(--button-bg);
}

.tab-button:hover:not(.active) {
  background-color: var(--card-bg);
}

/* ---------- Modal (nx-) ---------- */
.nx-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.45);
  backdrop-filter: blur(2px);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  z-index: 1000;
}

@keyframes nx-fade-up {
  from { opacity: 0; transform: translateY(10px) scale(0.98); }
  to   { opacity: 1; transform: translateY(0) scale(1); }
}

.nx-modal {
  background: var(--card-bg);
  color: var(--text-color);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  box-shadow: 0 12px 30px var(--shadow);
  width: 100%;
  max-width: 700px;
  max-height: 92vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  animation: nx-fade-up 0.18s ease-out;
}

.nx-modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1.25rem;
  border-bottom: 1px solid var(--border-color);
  background: var(--header-bg);
}

.nx-modal-title {
  margin: 0;
  font-size: 1.25rem;
  color: var(--text-color);
}

.nx-modal-close {
  border: none;
  background: transparent;
  color: var(--text-color);
  font-size: 1.25rem;
  cursor: pointer;
  border-radius: 8px;
  width: 36px;
  height: 36px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.nx-modal-close:hover {
  background: var(--card-bg);
}

.nx-modal-body {
  padding: 1rem 1.25rem;
  overflow: auto;
}

/* ---------- Custom Confirm/Alert Dialogs ---------- */
.nx-dialog-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  z-index: 9999;
  animation: nx-dialog-fade-in 0.15s ease-out;
}

@keyframes nx-dialog-fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

.nx-dialog {
  background: var(--card-bg);
  color: var(--text-color);
  border-radius: 12px;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.35);
  width: 100%;
  max-width: 420px;
  overflow: hidden;
  animation: nx-dialog-scale-in 0.18s ease-out;
}

@keyframes nx-dialog-scale-in {
  from { 
    opacity: 0; 
    transform: scale(0.92) translateY(-10px); 
  }
  to { 
    opacity: 1; 
    transform: scale(1) translateY(0); 
  }
}

.nx-dialog-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1.25rem 1.5rem 1rem;
  border-bottom: 1px solid var(--border-color);
}

.nx-dialog-icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.nx-dialog-icon.warning {
  background: rgba(245, 158, 11, 0.15);
  color: #f59e0b;
}

.nx-dialog-icon.danger {
  background: rgba(239, 68, 68, 0.15);
  color: #ef4444;
}

.nx-dialog-icon.info {
  background: rgba(59, 130, 246, 0.15);
  color: #3b82f6;
}

.nx-dialog-icon.success {
  background: rgba(16, 185, 129, 0.15);
  color: #10b981;
}

.nx-dialog-icon.error {
  background: rgba(239, 68, 68, 0.15);
  color: #ef4444;
}

.nx-dialog-title {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--text-color);
}

.nx-dialog-body {
  padding: 1rem 1.5rem 1.5rem;
}

.nx-dialog-message {
  margin: 0;
  font-size: 0.95rem;
  line-height: 1.5;
  color: var(--text-secondary);
  white-space: pre-wrap;
}

.nx-dialog-footer {
  display: flex;
  gap: 0.75rem;
  justify-content: flex-end;
  padding: 0 1.5rem 1.25rem;
}

.nx-dialog-btn {
  padding: 0.6rem 1.25rem;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  border: none;
  transition: all 0.15s ease;
}

.nx-dialog-btn:focus {
  outline: 2px solid var(--button-bg);
  outline-offset: 2px;
}

.nx-dialog-btn.cancel {
  background: var(--card-bg);
  color: var(--text-color);
  border: 1px solid var(--border-color);
}

.nx-dialog-btn.cancel:hover {
  background: var(--hover-bg);
}

.nx-dialog-btn.confirm {
  background: var(--button-bg);
  color: white;
}

.nx-dialog-btn.confirm:hover {
  opacity: 0.9;
}

.nx-dialog-btn.confirm.danger {
  background: #ef4444;
}

.nx-dialog-btn.confirm.warning {
  background: #f59e0b;
}

.nx-dialog-btn.ok {
  background: var(--button-bg);
  color: white;
  min-width: 80px;
}

.nx-dialog-btn.ok:hover {
  opacity: 0.9;
}

/* ---------- Form layout ---------- */
.nx-form-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.nx-field {
  display: flex;
  flex-direction: column;
  gap: 0.35rem;
}

.nx-span-2 {
  grid-column: span 2;
}

.nx-label {
  font-size: 0.9rem;
  color: var(--text-color);
  opacity: 0.9;
}

.nx-input,
.nx-select,
.nx-textarea {
  width: 100%;
  padding: 0.5rem 0.2rem;
  border: 1px solid var(--border-color);
  background: var(--card-bg);
  color: var(--text-color);
  border-radius: 0.4rem;
  box-shadow: inset 0 1px 2px var(--shadow);
}

.nx-input:focus,
.nx-select:focus,
.nx-textarea:focus {
  outline: none;
  border-color: var(--button-bg);
  box-shadow: 0 0 0 3px rgba(59,130,246,0.2);
}

/* Placeholder text styling */
.nx-input::placeholder,
.nx-select::placeholder,
.nx-textarea::placeholder {
  color: var(--text-muted);
  opacity: 1;
}

/* Dropdown options styling */
.nx-select option {
  background-color: var(--card-bg);
  color: var(--text-color);
}

.nx-textarea {
  resize: vertical;
  min-height: 90px;
}

/* ---------- Actions ---------- */
.nx-actions {
  margin-top: 1rem;
  display: flex;
  gap: 0.5rem;
  justify-content: flex-end;
}

.button-secondary {
  background-color: #6c757d;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.button-secondary:hover {
  background-color: #5a6268;
}

/* ---------- Responsive ---------- */
@media (max-width: 720px) {
  .nx-form-grid { grid-template-columns: 1fr; }
  .nx-span-2 { grid-column: span 1; }
  .nx-modal { max-height: 90vh; }
}

/* ---------- CategoryPicker (nx-catpicker) ---------- */
.nx-catpicker {
  position: relative;
}

.nx-catpicker-input {
  min-height: 40px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 6px;
  padding: 6px 40px 6px 8px;
  border: 1px solid var(--border-color);
  background: var(--card-bg);
  color: var(--text-color);
  border-radius: 8px;
  cursor: text;
  box-shadow: inset 0 1px 2px var(--shadow);
}

.nx-catpicker-input.open {
  border-color: var(--button-bg);
  box-shadow: 0 0 0 3px rgba(59,130,246,0.2);
}

.nx-catpicker-hidden-input {
  border: none;
  outline: none;
  background: transparent;
  color: var(--text-color);
  padding: 4px;
  min-width: 120px;
  flex: 1;
}

.nx-catpicker-clear {
  position: absolute;
  right: 8px;
  top: 8px;
  height: 24px;
  padding: 0 8px;
  background: var(--button-bg);
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.8rem;
}

.nx-chip {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 2px 8px;
  border-radius: 9999px;
  background: var(--header-bg);
  border: 1px solid var(--border-color);
  color: var(--text-color);
  font-size: 0.85rem;
}

.nx-chip.primary {
  background: #fffbeb;
  border-color: #fbbf24;
  color: #92400e;
}

body.dark .nx-chip.primary {
  background: #3a2d00;
  border-color: #fbbf24;
  color: #ffdd57;
}

.nx-chip.ghost {
  opacity: 0.7;
  font-style: italic;
}

.nx-chip-x,
.nx-chip-star {
  border: none;
  background: transparent;
  color: inherit;
  cursor: pointer;
  padding: 0;
  line-height: 1;
}

.nx-chip-x:hover { color: #dc3545; }
.nx-chip-star:hover { color: #f59e0b; }

.nx-catpicker-dropdown {
  position: absolute;
  left: 0;
  right: 0;
  top: calc(100% + 4px);
  background: var(--card-bg);
  color: var(--text-color);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  box-shadow: 0 8px 20px var(--shadow);
  z-index: 100;
  overflow: hidden;
}

.nx-catpicker-search {
  padding: 0.5rem;
  border-bottom: 1px solid var(--border-color);
}

.nx-catpicker-search input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid var(--border-color);
  background: var(--card-bg);
  color: var(--text-color);
  border-radius: 6px;
}

.nx-catpicker-list {
  max-height: 260px;
  overflow: auto;
}

.nx-catpicker-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.45rem 0.6rem;
  border-bottom: 1px solid var(--border-color);
}

.nx-catpicker-item:last-child {
  border-bottom: none;
}

.nx-catpicker-item.selected {
  background: rgba(59,130,246,0.08);
}

.nx-catpicker-name {
  flex: 1;
}

.nx-catpicker-star {
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: 1rem;
  color: var(--text-color);
  opacity: 0.6;
}

.nx-catpicker-star.active {
  color: #f59e0b;
}

.nx-catpicker-empty {
  padding: 0.75rem;
  font-size: 0.9rem;
  color: var(--text-color);
  opacity: 0.7;
}

@media (max-width: 720px) {
  .nx-catpicker-input { padding-right: 34px; }
  .nx-catpicker-clear { top: 6px; }
}

/* ---------- Plex page enhancements ---------- */
.nx-plex-card h2 { margin-top: 0; }

.nx-plex-steps {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  align-items: center;
  margin: 0.25rem 0 1rem;
}

.nx-step {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 2px 10px;
  background: var(--header-bg);
  border: 1px solid var(--border-color);
  border-radius: 9999px;
  font-size: 0.85rem;
}

.nx-badge {
  width: 18px;
  height: 18px;
  border-radius: 9999px;
  background: var(--button-bg);
  color: #fff;
  display: inline-grid;
  place-items: center;
  font-size: 0.75rem;
  font-weight: 700;
}

/* Method sections */
.nx-plex-method {
  border-left: 4px solid var(--button-bg);
  background: var(--card-bg);
}

/* Info note */
.nx-plex-note {
  background: var(--header-bg);
}

/* Button variants */
.button-success { background-color: #28a745 !important; }
.button-danger  { background-color: #dc3545 !important; }
.button-warn    { background-color: #f59e0b !important; }
.button-outline {
  background: transparent !important;
  color: var(--text-color) !important;
  border: 1px solid var(--border-color) !important;
}

/* Status chip */
/* Status chip */
.nx-chip.nx-status {
  padding: 0.1rem 0.5rem;
  border-radius: 9999px;
  border: 1px solid var(--border-color);
  background: var(--header-bg);
  font-size: 0.85rem;
}

body.light .nx-chip.nx-status.ok {
  background: rgba(40,167,69,0.15);
  border-color: rgba(40,167,69,0.45);
  color: #1e7e34;
}

body.dark .nx-chip.nx-status.ok {
