/* Collapsible help */
details.nx-plex-help { margin-top: 0.25rem; }
details.nx-plex-help summary { cursor: pointer; color: var(--button-bg); }
details.nx-plex-help[open] {
  background: var(--card-bg);
  border: 1px dashed var(--border-color);
  border-radius: 8px;
  padding: 0.5rem 0.75rem;
}

/* Sticky top navigation refinement for better UX on scroll */
.tabbar-right { display: flex; align-items: center; }

/* Preroll card header and actions: prevent overflow on long titles */
.preroll-header { display: flex; justify-content: space-between; align-items: flex-start; gap: 0.5rem; min-width: 0; }
.preroll-title { white-space: nowrap; overflow: hidden; text-overflow: ellipsis; min-width: 0; }
.preroll-actions { display: flex; gap: 0.25rem; flex-shrink: 0; }

/* Preroll list row title clamp */
.preroll-row-title { white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }

/* Inline create button row for CategoryPicker dropdown */
.nx-catpicker-create {
  padding: 0.5rem;
  border-bottom: 1px solid var(--border-color);
  background: var(--card-bg);
}

.nx-catpicker-create .button {
  font-size: 0.85rem;
  padding: 0.35rem 0.6rem;
}

/* ---------- Map Root Loading Indicator (theme-aware) ---------- */
.nx-map-progress {
  padding: 0.5rem 0.75rem;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  box-shadow: 0 2px 4px var(--shadow);
}

.nx-spinner {
  width: 16px;
  height: 16px;
  border: 2px solid var(--border-color);
  border-top-color: var(--button-bg);
  border-right-color: var(--button-bg);
  border-radius: 50%;
  animation: nx-spin 0.7s linear infinite;
}

@keyframes nx-spin {
  to { transform: rotate(360deg); }
}

/* Spin animation for Lucide icons */
.spin {
  animation: nx-spin 1s linear infinite;
}

.nx-progress {
  position: relative;
  height: 6px;
  background: var(--header-bg);
  border: 1px solid var(--border-color);
  border-radius: 9999px;
  overflow: hidden;
}

.nx-progress .bar {
  position: absolute;
  left: -40%;
  width: 40%;
  height: 100%;
  background: var(--button-bg);
  border-radius: inherit;
  animation: nx-indeterminate 1.2s ease-in-out infinite;
}

@keyframes nx-indeterminate {
  0%   { left: -40%; }
  50%  { left: 60%; }
  100% { left: 100%; }
}

/* Enhanced import progress styles */
.nx-import-progress {
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Folder browser button hover states */
.nx-folder-item {
  transition: background-color 0.15s ease, border-color 0.15s ease;
}

.nx-folder-item:hover {
  background-color: var(--header-bg) !important;
  border-color: var(--button-bg) !important;
}

/* Mobile responsive for import section */
@media (max-width: 768px) {
  .upload-section [style*="grid-template-columns: 1fr 1fr"] {
    grid-template-columns: 1fr !important;
  }
}

/* Rockerswitch toggle */
.nx-rockerswitch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px;
  background-color: var(--border-color);
  border-radius: 24px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.nx-rockerswitch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.nx-rockerswitch-slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--border-color);
  border-radius: 24px;
  transition: background-color 0.3s ease;
}

.nx-rockerswitch-slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 50%;
  transition: transform 0.3s ease;
}

input:checked + .nx-rockerswitch-slider {
  background-color: var(--button-bg);
}

input:checked + .nx-rockerswitch-slider:before {
  transform: translateX(26px);
}

/* Respect users who prefer reduced motion */
@media (prefers-reduced-motion: reduce) {
  .nx-spinner { animation: none; }
  .nx-progress .bar { animation: none; left: 0; width: 100%; }
  .nx-rockerswitch-slider { transition: none; }
  .nx-rockerswitch-slider:before { transition: none; }
}

/* Modern Enhanced Prerolls UI (2025-10-01 v2) â€” softer, tonal, theme-consistent */
.prerolls-control-bar {
  --ctl-h: 44px;
  --ctl-radius: 12px;
  display: flex;
  gap: 1rem 1.25rem;
  margin-bottom: 1rem;
  padding: 0.75rem 1rem;
  background: var(--header-bg);
  border-radius: 14px;
  border: 1px solid var(--border-color);
  box-shadow: 0 1px 3px var(--shadow);
  flex-wrap: wrap;
  align-items: center;
}

.control-group {
  display: flex;
  flex-direction: column;
  gap: 0.45rem;
