  align-items: center;
  gap: 0.5rem;
  height: var(--ctl-h);
  padding: 0 0.9rem;
  background: var(--header-bg);
  border: 1px solid var(--border-color);
  border-radius: 9999px;
  cursor: pointer;
  font-size: 0.85rem;
  color: var(--text-color);
  transition: background-color 0.2s ease, color 0.2s ease, transform 0.08s ease, box-shadow 0.2s ease, border-color 0.2s ease;
}

.view-btn:hover {
  background: var(--card-bg);
  box-shadow: 0 1px 3px var(--shadow);
  transform: translateY(-1px);
}

.view-btn.active {
  background: var(--button-bg);
  color: #fff;
  border-color: transparent;
  box-shadow: 0 6px 12px rgba(0,0,0,0.12);
}

.view-btn:active {
  transform: translateY(0);
  box-shadow: 0 3px 8px rgba(0,0,0,0.12);
}

.view-btn:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px rgba(59,130,246,0.25);
}

.view-icon { font-size: 1rem; }

/* Inputs: dropdowns and text fields with custom arrow and focus ring */
.filter-controls {
  display: flex;
  gap: 0.6rem;
  align-items: center;
}

.filter-select,
.page-size-select,
.bulk-category-select {
  height: var(--ctl-h);
  padding: 0 2rem 0 0.75rem;
  border: 1px solid var(--border-color);
  border-radius: var(--ctl-radius);
  background-color: var(--header-bg);
  color: var(--text-color);
  font-size: 0.9rem;
  min-width: 150px;
  transition: border-color 0.2s ease, box-shadow 0.2s ease, transform 0.08s ease, background-color 0.2s ease;
  box-shadow: inset 0 1px 2px var(--shadow);
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='%23666' viewBox='0 0 16 16'><path d='M4 6l4 4 4-4' stroke='%23666' stroke-width='1.5' fill='none' stroke-linecap='round' stroke-linejoin='round'/></svg>");
  background-repeat: no-repeat;
  background-position: right 0.65rem center;
  background-size: 16px 16px;
}

.filter-select:hover,
.page-size-select:hover,
.bulk-category-select:hover {
  border-color: var(--button-bg);
}

.filter-select:focus,
.page-size-select:focus,
.bulk-category-select:focus {
  outline: none;
  border-color: var(--button-bg);
  box-shadow: 0 0 0 3px rgba(59,130,246,0.2);
}

/* Category select for Discovery section */
.category-select {
  background-color: var(--card-bg);
  color: var(--text-color);
  border: 2px solid var(--border-color);
}

.category-select option {
  background-color: var(--card-bg);
  color: var(--text-color);
  padding: 0.5rem;
}

body.dark .category-select option {
  background-color: #2d2d2d;
  color: #e0e0e0;
}

body.light .category-select option {
  background-color: white;
  color: #333;
}

.search-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.search-input {
  height: var(--ctl-h);
  padding: 0 2rem 0 0.75rem;
  border: 1px solid var(--border-color);
  border-radius: var(--ctl-radius);
  background: var(--header-bg);
  color: var(--text-color);
  font-size: 0.9rem;
  width: 220px;
  transition: border-color 0.2s ease, box-shadow 0.2s ease, background-color 0.2s ease;
  box-shadow: inset 0 1px 2px var(--shadow);
}

.search-input::placeholder {
