  box-shadow: 0 0 0 3px rgba(59,130,246,0.2);
}

.search-icon {
  position: absolute;
  right: 0.6rem;
  color: var(--text-secondary);
  pointer-events: none;
}

/* Primary action (Apply) */
.filter-btn {
  height: var(--ctl-h);
  padding: 0 1rem;
  background: var(--button-bg);
  color: #fff;
  border: 1px solid transparent;
  border-radius: var(--ctl-radius);
  cursor: pointer;
  font-size: 0.85rem;
  font-weight: 600;
  transition: transform 0.08s ease, filter 0.2s ease, box-shadow 0.2s ease;
  box-shadow: 0 6px 12px rgba(0,0,0,0.12);
}

.filter-btn:hover { filter: brightness(1.05); }
.filter-btn:active { transform: translateY(1px); box-shadow: 0 3px 8px rgba(0,0,0,0.12); }
.filter-btn:focus-visible { outline: none; box-shadow: 0 0 0 3px rgba(59,130,246,0.25); }

/* Bulk bar and actions */
.bulk-actions-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem 1rem;
  background: var(--header-bg);
  border-radius: 14px;
  border: 1px solid var(--border-color);
  margin-bottom: 1rem;
  box-shadow: 0 1px 3px var(--shadow);
  flex-wrap: wrap;
