  align-items: center;
  gap: 0.75rem;
}

.select-all-wrapper { display: flex; align-items: center; gap: 0.5rem; cursor: pointer; }
.select-all-checkbox { width: 1rem; height: 1rem; cursor: pointer; }
.select-all-label { font-size: 0.9rem; color: var(--text-color); font-weight: 500; }

.action-btn {
  height: var(--ctl-h);
  padding: 0 0.85rem;
  border: 1px solid var(--border-color);
  border-radius: var(--ctl-radius);
  cursor: pointer;
  font-size: 0.85rem;
  font-weight: 600;
  transition: transform 0.08s ease, filter 0.2s ease, box-shadow 0.2s ease, background-color 0.2s ease, border-color 0.2s ease;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--header-bg);
  color: var(--text-color);
  box-shadow: 0 1px 2px var(--shadow);
}

.action-btn:hover:not(:disabled) { filter: brightness(1.03); box-shadow: 0 2px 4px var(--shadow); }
.action-btn:active:not(:disabled) { transform: translateY(1px); box-shadow: 0 1px 2px var(--shadow); }

.action-btn:disabled { opacity: 0.6; cursor: not-allowed; }

.action-btn.secondary {
  background: var(--header-bg);
  color: var(--text-color);
  border-color: var(--border-color);
}
.action-btn.primary {
  background: #28a745;
  color: #fff;
  border-color: transparent;
}
.action-btn.danger {
  background: #dc3545;
  color: #fff;
  border-color: transparent;
}

/* Dark theme refinements */
body.dark .prerolls-control-bar,
body.dark .bulk-actions-bar {
  background: var(--card-bg);
  border-color: var(--border-color);
  box-shadow: 0 2px 8px var(--shadow);
}

body.dark .view-toggle { background: var(--card-bg); border-color: var(--border-color); }
body.dark .view-btn { background: var(--card-bg); }
body.dark .view-btn:hover { background: var(--header-bg); }

body.dark .filter-select,
body.dark .page-size-select,
body.dark .bulk-category-select,
body.dark .search-input {
  background-color: var(--card-bg);
  border-color: var(--border-color);
  color: var(--text-color);
  box-shadow: inset 0 1px 2px var(--shadow);
}

/* Dark theme: higher-contrast arrow */
body.dark .filter-select,
body.dark .page-size-select,
body.dark .bulk-category-select {
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='16' height='16' fill='%23bbb' viewBox='0 0 16 16'><path d='M4 6l4 4 4-4' stroke='%23bbb' stroke-width='1.5' fill='none' stroke-linecap='round' stroke-linejoin='round'/></svg>");
}

/* Better icon contrast on focus */
.search-input-wrapper:focus-within .search-icon { color: var(--button-bg); }

/* Accessibility: reduced motion */
@media (prefers-reduced-motion: reduce) {
  .view-btn, .filter-btn, .action-btn {
    transition: none;
