}

/* Responsive adjustments for mobile */
@media (max-width: 768px) {
  .prerolls-control-bar { flex-direction: column; align-items: stretch; gap: 1rem; }
  .filter-controls { flex-direction: column; align-items: stretch; }
  .search-input { width: 100%; }
  .bulk-actions-bar { flex-direction: column; align-items: stretch; gap: 0.75rem; }
  .bulk-actions-left, .bulk-actions-right { justify-content: center; }
  .bulk-category-wrapper { flex-direction: column; align-items: stretch; }
}

/* Shared Toolbar + Checkbox styles (Categories + Schedule Management) */
.nx-toolbar {
  display: flex;
  gap: 0.75rem 1rem;
  align-items: center;
  flex-wrap: wrap;
  padding: 0.75rem 1rem;
  margin-bottom: 1rem;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  box-shadow: 0 2px 10px var(--shadow);
}

.toolbar-group {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.nx-checkrow {
  display: inline-flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.nx-check {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.35rem 0.6rem;
  background: var(--header-bg);
  border: 1px solid var(--border-color);
  border-radius: 10px;
  font-size: 0.9rem;
  color: var(--text-color);
  transition: background-color 0.15s ease, border-color 0.15s ease;
}

.nx-check:hover {
  background: var(--card-bg);
}

.nx-check input {
  width: 1rem;
  height: 1rem;
  accent-color: var(--button-bg);
}

/* Dark theme refinements */
body.dark .nx-toolbar {
  background: var(--card-bg);
  border-color: var(--border-color);
  box-shadow: 0 2px 10px var(--shadow);
}

body.dark .nx-check {
  background: var(--card-bg);
  border-color: var(--border-color);
  color: var(--text-color);
}

/* Responsive */
@media (max-width: 768px) {
  .nx-toolbar { flex-direction: column; align-items: stretch; }
  .toolbar-group { width: 100%; }
  .nx-checkrow { gap: 0.75rem; }
