/* ---------- Icon buttons (micro-visual treatment for small actions in cards/lists) ---------- */
.nx-iconbtn {
  height: 28px;
  min-height: 28px;
  padding: 0 0.5rem;
  border-radius: 9999px;
  border: 1px solid var(--border-color);
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(0,0,0,0.02));
  color: var(--text-color);
  font-size: 0.82rem;
  line-height: 1;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.35rem;
  cursor: pointer;
  box-shadow: 0 1px 3px var(--shadow);
  transition: transform 0.08s ease, box-shadow 0.2s ease, filter 0.2s ease, background-color 0.2s ease, border-color 0.2s ease;
}

.nx-iconbtn:hover {
  filter: brightness(1.03);
  transform: translateY(-1px);
  box-shadow: 0 2px 6px var(--shadow);
}

.nx-iconbtn:active {
  transform: translateY(0);
  box-shadow: 0 1px 3px var(--shadow);
}

.nx-iconbtn:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px rgba(59,130,246,0.25);
}

