.nx-iconbtn--danger {
  background: linear-gradient(180deg, #dc3545, #c82333);
  color: #fff;
  border-color: transparent;
}
.nx-iconbtn--danger:hover { filter: brightness(1.05); }

.nx-iconbtn--muted {
  background: #6c757d;
  color: #fff;
  border-color: transparent;
}

/* Disabled */
.nx-iconbtn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
  filter: none;
  box-shadow: 0 1px 3px var(--shadow);
}

/* Dark mode refinements */
body.dark .nx-iconbtn {
  background: linear-gradient(180deg, rgba(255,255,255,0.04), rgba(0,0,0,0.06));
  border-color: var(--border-color);
  color: var(--text-color);
}

body.dark .nx-iconbtn--danger {
  background: linear-gradient(180deg, #e03131, #c92a2a);
}

body.dark .nx-iconbtn--muted {
  background: #495057;
}

/* Reduced motion */
@media (prefers-reduced-motion: reduce) {
  .nx-iconbtn { transition: none; }
}

/* ========== Dashboard Spotlight Cards (2025-10-02) ========== */
/* Goal: Make dashboard cards eye-catching, readable at-a-glance, and theme-consistent */
.dashboard .grid .card {
  position: relative;
  overflow: hidden;
  border-radius: 14px;
  border: 1px solid var(--border-color);
  background: var(--card-bg);
  box-shadow: 0 2px 6px var(--shadow);
  transition: transform 0.12s ease, box-shadow 0.2s ease, border-color 0.2s ease, background-color 0.2s ease;
}

/* Accent bar at the top of each card (color is set via --nx-accent below) */
.dashboard .grid .card::before {
  content: "";
  position: absolute;
  top: 0; left: 0; right: 0;
  height: 4px;
  background: var(--nx-accent, var(--button-bg));
}

/* Gentle background vignette for depth */
.dashboard .grid .card::after {
  content: "";
  position: absolute;
  inset: 0;
  pointer-events: none;
  background: radial-gradient(110% 60% at 110% -10%, rgba(0,0,0,0.06), transparent 50%);
  opacity: 0.6;
}

/* Lift on hover for prominence */
.dashboard .grid .card:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px var(--shadow);
  border-color: var(--nx-accent, var(--border-color));
}

/* Titles: slightly tighter, stronger hierarchy */
.dashboard .grid .card h2 {
  margin-top: 0;
  margin-bottom: 0.35rem;
  font-size: 1.05rem;
  font-weight: 600;
  letter-spacing: 0.2px;
  display: flex;
  align-items: center;
  gap: 8px;
}

/* First paragraph in each card is treated as KPI */
.dashboard .grid .card > p:first-of-type {
  margin: 0.15rem 0 0.25rem 0;
  font-size: 1.3rem;
  line-height: 1.15;
  font-weight: 600;
  color: var(--text-color);
  letter-spacing: 0;
}

/* Secondary text (sub-lines) is slightly muted */
.dashboard .grid .card p + p {
  margin-top: 0.1rem;
  font-size: 0.92rem;
  color: var(--text-secondary);
  line-height: 1.2;
  margin-bottom: 0;
}

/* Ensure all paragraphs within cards have compact spacing */
.dashboard .grid .card p {
  margin: 0;
  padding: 0;
  line-height: 1.2;
}

/* Internal list/grids inside cards keep the depth but reduce borders a bit */
.dashboard .grid .card .nx-list,
.dashboard .grid .card .nx-subgrid {
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 10px;
}

/* Buttons inside cards: stand out but not overpower */
.dashboard .grid .card .button {
  box-shadow: 0 1px 3px var(--shadow);
}

/* Map a distinct accent to each primary dashboard card by order */
.dashboard .grid .card:nth-child(1) { /* Plex Status */
  --nx-accent: #0ea5e9; /* sky */
}
.dashboard .grid .card:nth-child(2) { /* Prerolls */
  --nx-accent: #8b5cf6; /* violet */
}
.dashboard .grid .card:nth-child(3) { /* Storage */
  --nx-accent: #14b8a6; /* teal */
}
.dashboard .grid .card:nth-child(4) { /* Schedules */
  --nx-accent: #10b981; /* emerald */
}
.dashboard .grid .card:nth-child(5) { /* Scheduler */
  --nx-accent: #f59e0b; /* amber */
}
.dashboard .grid .card:nth-child(6) { /* Current Category */
  --nx-accent: #f43f5e; /* rose */
}
.dashboard .grid .card:nth-child(7) { /* Upcoming Schedules (if shown) */
  --nx-accent: #6366f1; /* indigo */
}

/* Dark theme: a bit more shadow and less vignette opacity */
body.dark .dashboard .grid .card {
  box-shadow: 0 2px 10px var(--shadow);
}
body.dark .dashboard .grid .card::after {
  opacity: 0.4;
}
body.dark .dashboard .grid .card:hover {
  box-shadow: 0 8px 16px var(--shadow);
}

/* If status text is used as KPI, emphasize success/error colors clearly */
.dashboard .grid .card > p:first-of-type .ok,
.dashboard .grid .card > .ok {
  color: var(--success-color);
}
.dashboard .grid .card > p:first-of-type .bad,
.dashboard .grid .card > .bad {
  color: var(--error-color);
}

/* Optional micro helpers for future JSX tweaks (non-breaking if unused) */
.nx-card-title { margin: 0 0 0.35rem 0; font-size: 1.05rem; }
.nx-card-meta { font-size: 0.92rem; color: var(--text-secondary); }
.nx-card-action { border: 1px solid var(--border-color); background: transparent; color: var(--text-color); }
.nx-card-action:hover { background: var(--header-bg); }
.nx-kpi { display: flex; align-items: baseline; gap: 0.5rem; }
.nx-kpi-value { font-size: 1.3rem; font-weight: 600; line-height: 1.1; color: var(--text-color); }
.nx-kpi-label, .nx-kpi-sub { font-size: 0.92rem; color: var(--text-secondary); }
.nx-kpi-value.ok { color: var(--success-color); }
.nx-kpi-value.bad { color: var(--error-color); }

/* Responsive: keep cards readable on small screens */
@media (max-width: 768px) {
  .dashboard .grid .card > p:first-of-type { font-size: 1.2rem; }
}

/* Prerolls stats spacing fix (2025-10-02) */
.prerolls-stats {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
}
.prerolls-stats .stat-item {
  display: inline-block;
}

/* ========== Resizable Dashboard (React Grid Layout) ========== */
.resizable-dashboard-wrapper {
  padding: 1rem;
  background: var(--bg-color);
  transition: background-color 0.3s ease;
}

.dashboard-controls {
  display: flex;
  gap: 0.75rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.edit-toggle-btn,
.reset-layout-btn {
  background: var(--button-bg);
  color: white;
  border: none;
  border-radius: 0.5rem;
  padding: 0.6rem 1rem;
  cursor: pointer;
  font-weight: 600;
  font-size: 0.9rem;
  transition: all 0.2s ease;
  box-shadow: 0 2px 4px var(--shadow);
}

.edit-toggle-btn:hover,
.reset-layout-btn:hover {
  background: var(--button-hover);
  box-shadow: 0 4px 8px var(--shadow);
  transform: translateY(-1px);
}

.edit-toggle-btn:active,
.reset-layout-btn:active {
  transform: translateY(0);
  box-shadow: 0 1px 2px var(--shadow);
}

.reset-layout-btn {
  background: #f59e0b;
}

.reset-layout-btn:hover {
  background: #d97706;
}

/* React Grid Layout base styles */
.dashboard-grid-layout {
  background: var(--bg-color);
  position: relative;
}

.react-grid-layout {
  position: relative;
  transition: height 200ms ease;
}

.react-grid-item {
  transition: all 200ms ease;
  transition-property: left, top, width, height;
}

.react-grid-item img {
  pointer-events: none;
  user-select: none;
}

.react-grid-item.cssTransforms {
  transition-property: transform, width, height;
}

.react-grid-item.resizing {
  opacity: 0.9;
  z-index: 1;
  will-change: width, height;
}

.react-grid-item.react-draggable-dragging {
  transition: none;
  z-index: 3;
  will-change: transform;
  opacity: 0.9;
  box-shadow: 0 10px 30px var(--shadow);
}

.react-grid-item.dropping {
  visibility: hidden;
}

.react-grid-item.react-grid-placeholder {
  background: var(--button-bg);
  opacity: 0.2;
  border-radius: 0.5rem;
  z-index: 2;
  border: 2px dashed var(--button-bg);
}

.react-grid-item.static {
  background: var(--header-bg);
  opacity: 0.8;
}

.react-grid-item .resizing {
  opacity: 1;
}

.react-grid-item .text {
  font-size: 24px;
}

.react-grid-item .minMax {
  font-size: 12px;
}

.react-grid-item .add {
  cursor: pointer;
}

.react-draggable-dragging {
  opacity: 1;
}

.react-grid-item.react-grid-placeholder {
  display: block;
}

/* Resizable handle */
.react-resizable-hide > .react-resizable-handle {
  display: none;
}

.react-resizable-handle {
  position: absolute;
  width: 20px;
  height: 20px;
  background: url('data:image/svg+xml;base64,') no-repeat;
  background-position: bottom right;
  padding: 0;
  background-repeat: no-repeat;
  background-origin: content-box;
  box-sizing: border-box;
  cursor: se-resize;
  right: 0;
  bottom: 0;
}

/* Dashboard Tile Container */
.dashboard-tile-container {
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 0.75rem;
  box-shadow: 0 2px 6px var(--shadow);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transition: all 0.2s ease;
  position: relative;
}

.dashboard-tile-container:hover {
  box-shadow: 0 8px 16px var(--shadow);
  border-color: var(--button-bg);
}

.dashboard-tile-container.edit-mode {
  border-color: var(--button-bg);
  border-style: dashed;
  background: var(--header-bg);
  cursor: move;
}

.dashboard-tile-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem 1rem;
  background: linear-gradient(135deg, rgba(255,255,255,0.1), rgba(0,0,0,0.05));
  border-bottom: 1px solid var(--border-color);
  flex-shrink: 0;
}

.tile-title {
  margin: 0;
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-color);
  flex: 1;
}

.tile-drag-handle {
  color: var(--text-secondary);
  font-size: 0.8rem;
  cursor: grab;
  user-select: none;
  padding: 0 0.5rem;
}

.tile-drag-handle:active {
  cursor: grabbing;
}

.tile-settings-btn {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  padding: 0.25rem 0.5rem;
  color: var(--text-secondary);
  transition: all 0.2s ease;
  border-radius: 0.3rem;
}

.tile-settings-btn:hover {
  background: var(--button-bg);
  color: white;
  transform: scale(1.1);
}

.dashboard-tile-content {
  flex: 1;
  padding: 1rem;
  overflow-y: auto;
  overflow-x: hidden;
  color: var(--text-color);
  font-size: 0.9rem;
  line-height: 1.5;
}

.dashboard-tile-content::-webkit-scrollbar {
  width: 6px;
}

.dashboard-tile-content::-webkit-scrollbar-track {
  background: var(--header-bg);
  border-radius: 10px;
}

.dashboard-tile-content::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 10px;
}

.dashboard-tile-content::-webkit-scrollbar-thumb:hover {
  background: var(--button-bg);
}

/* Dark mode adjustments */
body.dark .dashboard-tile-container {
  box-shadow: 0 2px 10px var(--shadow);
}

body.dark .dashboard-tile-container:hover {
  box-shadow: 0 8px 20px var(--shadow);
}

body.dark .react-grid-item.react-grid-placeholder {
  background: var(--button-bg);
  opacity: 0.15;
}

/* Responsive */
@media (max-width: 768px) {
  .react-grid-layout {
    width: 100%;
  }

  .dashboard-tile-container {
    min-height: 200px;
  }

  .dashboard-controls {
    flex-direction: column;
  }

  .edit-toggle-btn,
  .reset-layout-btn {
    width: 100%;
  }
}

/* ========== Widget Settings Modal ========== */
.widget-settings-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(2px);
  animation: fadeIn 0.2s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.widget-settings-modal {
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 0.75rem;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.3);
  max-width: 500px;
  width: 90%;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  animation: slideUp 0.3s ease-out;
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes progressSlide {
  0% {
    background-position: 0% 0%;
  }
  100% {
    background-position: 200% 0%;
  }
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.25rem;
  border-bottom: 1px solid var(--border-color);
  background: var(--header-bg);
  flex-shrink: 0;
}

.modal-header h2 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-color);
}

.modal-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--text-secondary);
  padding: 0.25rem 0.5rem;
  border-radius: 0.3rem;
  transition: all 0.2s ease;
}

.modal-close:hover {
  background: var(--border-color);
  color: var(--text-color);
}

.modal-content {
  flex: 1;
  overflow-y: auto;
  padding: 1.5rem;
}

.settings-description {
  margin: 0 0 1rem 0;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.settings-options {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  margin-bottom: 1.5rem;
}

.settings-option {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.2s ease;
  background: var(--header-bg);
  border: 1px solid transparent;
}

.settings-option:hover {
  background: var(--border-color);
  border-color: var(--button-bg);
}

.settings-checkbox {
  width: 1.1rem;
  height: 1.1rem;
  cursor: pointer;
  accent-color: var(--button-bg);
}

.settings-label {
  font-weight: 500;
  color: var(--text-color);
}

.settings-preview {
  background: var(--header-bg);
  border: 1px solid var(--border-color);
  border-radius: 0.5rem;
  padding: 1rem;
  margin-top: 1rem;
}

.settings-preview h4 {
  margin: 0 0 0.75rem 0;
  font-size: 0.9rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.preview-content {
  color: var(--text-color);
  font-size: 0.9rem;
}

.preview-content ul {
  margin: 0;
  padding-left: 1.5rem;
  list-style: disc;
}

.preview-content li {
  margin-bottom: 0.5rem;
}

.preview-empty {
  color: var(--text-secondary);
  font-style: italic;
  margin: 0;
}

.modal-footer {
  display: flex;
  gap: 0.75rem;
  padding: 1rem 1.25rem;
  border-top: 1px solid var(--border-color);
  background: var(--header-bg);
  flex-shrink: 0;
  justify-content: flex-end;
}

.btn-cancel,
.btn-save {
  padding: 0.6rem 1.25rem;
  border: none;
  border-radius: 0.5rem;
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.btn-cancel {
  background: var(--border-color);
  color: var(--text-color);
}

.btn-cancel:hover {
  background: var(--button-bg);
  color: white;
}

.btn-save {
  background: var(--button-bg);
  color: white;
}

.btn-save:hover {
  background: var(--button-hover);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

.btn-save:active {
  transform: scale(0.98);
}

/* Modal responsive */
@media (max-width: 640px) {
  .widget-settings-modal {
    max-width: 95%;
    max-height: 90vh;
  }

  .modal-header {
    padding: 1rem;
