  }

  /* Modals */
  .nx-modal-overlay {
    padding: 0.5rem;
    align-items: flex-start;
    padding-top: 2rem;
  }

  .nx-modal {
    max-width: 100%;
    max-height: 95vh;
    border-radius: 8px;
  }

  .nx-modal-header {
    padding: 0.75rem 1rem;
  }

  .nx-modal-header h2 {
