  }
  
  input, select, textarea {
    font-size: 16px !important; /* Prevents iOS zoom on focus */
  }
  
  /* Schedule/Calendar cards */
  .schedule-card,
  .category-card {
    padding: 0.75rem;
  }
  
  /* Buttons - larger touch targets */
  button {
    min-height: 44px;
  }
  
  /* Action button groups */
  .action-buttons,
  .button-group {
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  
  .action-buttons button,
  .button-group button {
    flex: 1;
    min-width: 80px;
  }
  
  /* Dashboard tiles */
  .nx-tile {
    min-height: 120px;
  }
  
  /* Preroll grid */
  .preroll-grid {
    grid-template-columns: repeat(2, 1fr) !important;
    gap: 0.5rem;
  }
  
  /* Preroll items */
  .preroll-item {
    padding: 0.5rem;
  }
  
  .preroll-thumbnail {
    width: 60px;
    height: 40px;
  }
  
  /* Calendar adjustments */
  .calendar-container {
    overflow-x: auto;
  }
  
  .calendar-grid {
    min-width: 600px;
    font-size: 0.7rem;
  }
  
  /* Settings page */
  .settings-section {
    padding: 0.75rem;
  }
  
  /* Tables - convert to card layout on mobile */
  table.preroll-table {
    display: block;
    width: 100% !important;
  }
  
  table.preroll-table thead {
    display: none; /* Hide table headers on mobile */
  }
  
  table.preroll-table tbody {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
  }
  
  table.preroll-table tr {
    display: flex;
    flex-direction: column;
    padding: 0.75rem;
    background: var(--card-bg);
    border: 1px solid var(--border-color);
    border-radius: 0.5rem;
    gap: 0.5rem;
  }
  
  table.preroll-table td {
    display: block;
    padding: 0.25rem 0 !important;
    border: none !important;
    text-align: left !important;
    white-space: normal !important;
    word-wrap: break-word;
  }
  
  /* First cell (name) - make it prominent */
  table.preroll-table td:first-child {
    font-size: 1rem;
    font-weight: 600;
    padding-bottom: 0.5rem !important;
    border-bottom: 1px solid var(--border-color) !important;
    margin-bottom: 0.25rem;
  }
  
  /* Hide checkbox column label area, keep checkbox */
  table.preroll-table td:has(input[type="checkbox"]) {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
  }
  
  /* Actions cell - make buttons row */
  table.preroll-table td:last-child {
    padding-top: 0.5rem !important;
    border-top: 1px solid var(--border-color) !important;
    margin-top: 0.25rem;
  }
  
  table.preroll-table td:last-child > div {
    justify-content: flex-start !important;
  }
  
  /* Badges in table - inline */
  table.preroll-table td span[style*="borderRadius"] {
    margin-right: 0.5rem;
  }

  /* Schedule list items */
  .schedule-list-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  
  .schedule-list-item .schedule-actions {
    width: 100%;
    justify-content: flex-end;
  }
  
  /* Connect page QR code */
  .qr-code-container {
    max-width: 200px;
    margin: 0 auto;
  }
  
  /* Community prerolls */
  .community-preroll-card {
    padding: 0.75rem;
  }
  
  /* Sequence builder */
  .sequence-block {
    padding: 0.5rem;
  }
  
  .sequence-timeline {
    overflow-x: auto;
  }

  /* ===== CATEGORIES PAGE MOBILE FIXES ===== */
  
  /* Page headers */
  .header, h1.header {
    font-size: 1.25rem !important;
    margin-bottom: 0.75rem !important;
  }
  
  /* Category/Schedule action buttons at top */
  div[style*="display: flex"][style*="gap"] > button.button,
  div[style*="display: flex"][style*="gap"] > button.button-secondary {
    padding: 0.5rem 0.75rem !important;
    font-size: 0.8rem !important;
    white-space: nowrap;
  }
  
  /* Search/Filter bar - stack vertically on mobile */
  div[style*="display: flex"][style*="gap: 1rem"][style*="marginBottom"] {
    flex-direction: column !important;
    gap: 0.5rem !important;
    padding: 0.75rem !important;
  }
  
  /* Search input full width on mobile */
  div[style*="display: flex"][style*="gap: 1rem"] input[type="text"][style*="width: 65%"] {
    width: 100% !important;
  }
  
  /* Filter dropdown full width on mobile */
  div[style*="display: flex"][style*="gap: 1rem"] select[style*="width: 20%"] {
    width: 100% !important;
  }
  
  /* View toggle - compact on mobile */
  .view-toggle {
    width: 100% !important;
    justify-content: center !important;
    margin-left: 0 !important;
  }
  
  .view-btn {
    flex: 1;
    justify-content: center;
    padding: 0.5rem !important;
    font-size: 0.85rem !important;
  }
  
  /* Category cards in grid view - PREVENT CUTOFF */
  .nx-category-card,
  [class*="category"][class*="card"],
  div[style*="borderRadius"][style*="padding"][style*="background"] {
    padding: 0.75rem !important;
    overflow: visible !important;
    min-height: auto !important;
    height: auto !important;
  }
  
  .nx-category-card h3,
  [class*="category"] h3 {
    font-size: 0.95rem !important;
    word-wrap: break-word !important;
    overflow-wrap: break-word !important;
  }
  
  /* Category card actions */
  .nx-category-card .nx-actions,
  .nx-category-card [class*="actions"] {
    flex-wrap: wrap;
    gap: 0.25rem;
  }
  
  .nx-category-card button {
    padding: 0.4rem 0.6rem !important;
    font-size: 0.75rem !important;
  }
  
  /* Category badges - allow wrapping */
  div[style*="display: flex"][style*="flexWrap: wrap"][style*="gap"] {
    flex-wrap: wrap !important;
  }
  
  /* Category dropdown menus - ensure they fit on screen */
  div[style*="position: absolute"][style*="minWidth"] {
    min-width: 150px !important;
    max-width: 200px !important;
    right: 0 !important;
    left: auto !important;
  }
  
  /* ===== SCHEDULES PAGE MOBILE FIXES ===== */
  
  /* Schedule cards */
  .nx-schedule-card,
  [class*="schedule"][class*="card"] {
    padding: 0.75rem !important;
    overflow: visible !important;
  }
  
  /* Schedule info layout */
  .schedule-info,
  [class*="schedule-info"] {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  /* Schedule actions */
  .schedule-actions,
  [class*="schedule-actions"] {
    flex-wrap: wrap;
    gap: 0.25rem;
    width: 100%;
    justify-content: flex-start !important;
  }
  
  /* Calendar view - make it scrollable */
  .calendar-wrapper,
  [class*="calendar"] {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
  
  /* ===== GENERAL MOBILE IMPROVEMENTS ===== */
  
  /* Icon buttons smaller on mobile */
  .nx-iconbtn {
    padding: 0.35rem !important;
    min-width: 32px !important;
    min-height: 32px !important;
  }
  
  /* Checkbox styling */
  input[type="checkbox"] {
    width: 20px;
    height: 20px;
    min-width: 20px;
  }
  
  /* Badge/tag styling */
  .badge, .tag, [class*="badge"], [class*="tag"] {
    font-size: 0.7rem !important;
    padding: 0.2rem 0.4rem !important;
  }
  
  /* Status indicators */
  [class*="status"] {
    font-size: 0.75rem;
  }
  
  /* Preroll count badges */
  .preroll-count,
  [class*="preroll-count"] {
    font-size: 0.75rem !important;
  }
  
  /* Flex containers - allow wrapping */
  [style*="display: flex"]:not(.tab-buttons):not(.tab-group) {
    flex-wrap: wrap;
  }
  
  /* Gap adjustments for inline flex */
  [style*="gap: 1rem"] {
    gap: 0.5rem !important;
  }
  
  /* Section padding */
  section, .section {
    padding: 0.5rem !important;
  }
  
  /* Make inline buttons wrap */
  .inline-actions,
  [class*="inline-actions"] {
    flex-wrap: wrap;
    gap: 0.25rem;
  }
}


/* Extra small devices */
@media (max-width: 480px) {
  .tab-buttons {
    padding: 0 0.25rem;
  }
  
  .tabbar-right img {
    height: 30px !important;
  }
  
  .card {
    padding: 0.75rem;
  }
  
  .card h2 {
    font-size: 1rem;
  }
  
  /* Even smaller preroll grid */
  .preroll-grid {
    grid-template-columns: 1fr !important;
  }
  
  /* Stack buttons vertically */
  .action-buttons,
  .button-group {
    flex-direction: column;
  }
  
  .action-buttons button,
  .button-group button {
    width: 100%;
  }
  
  /* Compact headers */
  h1, .header { font-size: 1.25rem !important; }
  h2 { font-size: 1.1rem !important; }
  h3 { font-size: 1rem !important; }
  
  /* Extra small - hide view toggle text */
  .view-btn .view-icon {
    margin-right: 0 !important;
  }
  
  .view-btn span:not(.view-icon) {
    display: none;
  }
  
  /* Category/Schedule buttons - icon only on very small */
  button[style*="display: flex"][style*="alignItems: center"] span:not(:first-child) {
    display: none;
  }
  
  /* Tighter padding everywhere */
  .content-area {
    padding: 0.25rem !important;
  }
  
  /* Search bar smaller text */
  input[type="text"][placeholder*="Search"] {
    font-size: 0.85rem !important;
    padding: 0.5rem !important;
  }
  
  /* Select dropdowns */
  select {
    font-size: 0.85rem !important;
    padding: 0.5rem !important;
  }
  
  /* Modal adjustments for very small screens */
  .nx-modal {
    width: 98vw !important;
    margin: 0.5rem !important;
  }
  
  .nx-modal-body {
    padding: 0.5rem !important;
  }
  
  .nx-modal-header {
    padding: 0.5rem 0.75rem !important;
  }
  
  .nx-modal-footer {
    padding: 0.5rem !important;
  }
}

/* Tablet/iPad specific */
@media (min-width: 769px) and (max-width: 1024px) {
  .tab-group .tab-button {
    padding: 0.5rem 0.75rem;
    font-size: 0.85rem;
  }
  
  .dashboard-grid {
    grid-template-columns: repeat(2, 1fr) !important;
  }
  
  .preroll-grid {
    grid-template-columns: repeat(3, 1fr) !important;
  }
}
